import React, {useState, useEffect} from 'react';
import axios from 'axios';
import CharacterCard from './CharacterCard'; 
import './App.css';

function CharacterList(){
    const [CharacterList, setCharacterList] = useState([]);
    const [searchName, setName] = useState('');

    useEffect(() => {
        axios.get('https://rickandmortyapi.com/api/character/')
        .then(res => {
            console.log(res.data.results);
            setCharacterList(res.data.results);
        })
        .catch(err => {
            console.log(err)
        })
    }, [])

const searchedList = CharacterList.filter( char =>{
    return char.name.toLowerCase().includes( searchName.toLowerCase() )
}) 
// indexOf((searchName)>-1)

    return(
    <div >
        <div clasName="headbox">
             <h2>Ricky and Morty Characters</h2>
             <form className="form">
             <input className="search" type="text" onChange={e=>setName(e.target.value)}/>
            <button className="searchbutton" type="button">Search</button>
            <button className="resetbutton" onClick={e=>setName(" ")}>Reset</button></form> 
            
            <div className="Charactercard">
               {searchedList.map((chars, id) =>{
                return <CharacterCard key={id} chars={chars}/>
               }).slice(0,6)}
            </div>
        </div> 
    </div>
    )
}

export default CharacterList