import React from 'react';
import './App.css';


 export default function CharacterCard(props) {
         return(
        <div className="card">
                <img src ={props.chars.image}  alt="prof.jpg" />
            <div className="cardContainer">
                <h3>{props.chars.name}</h3>
                <p>{props.chars.status}</p>
                <p>{props.chars.species}</p>
            </div>
        </div>
         );
     }
 
 